import React from 'react';
//npm install --save react-chartjs-2 chart.js
import {Line} from 'react-chartjs-2';
import Flex from '../StyledComponents/Flex';
import styled from 'styled-components';

const ChartFlex = styled(Flex)`
  margin-bottom: 15px;
  background-color: rgb(209,241,247);
  height: 45vh;
`;


const MainChart = ({chargesData, incomeData, dates}) => {

const state = {
  labels:  dates,
  datasets: [
    {
      label: 'Income',
      fill: false,
      lineTension: 0.5,
      backgroundColor: 'rgba(253,178,1,1)',
      borderColor: 'rgba(253,178,1,1)',
      borderWidth: 2,
      data: incomeData.map(x=>+x)
    },
    {
        label: 'Expenses',
        fill: false,
        lineTension: 0.5,
        backgroundColor: 'rgba(132,125,115,1)',
        borderColor: 'rgba(132,125,115,1)',
        borderWidth: 2,
        data: chargesData.map(x=>+x)
      }
  ]
}


    return (
      <Flex>
      <ChartFlex>
        <Line
          data={state}
          
          options={{
            plugins:{
              title:{
              display:true,
              text:'Money flow',
              fontSize:20
            },
            legend:{
              display:true,
              position:'top'
            },

            maintainAspectRatio: false,
          }
          }}
        />
        
      </ChartFlex>
      <Flex>
        <div className="date-filter-input">
            <input type="date" id="start-filter-interval"></input>
        </div>
        <div className="date-filter-input">
            <input type="date" id="end-filter-interval"></input>
        </div>        
      </Flex>
      </Flex>
    );
}

export default MainChart;